:root {
  --flex-items: 4;
  --flex-items-gap: 10px;
}

.flex-container {
  gap: var(--flex-items-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex-container-item {
  flex-basis: calc( (100% - (var(--flex-items)  - 1) * var(--flex-items-gap) ) / var(--flex-items) );
}

.flex-container-item img {
  width: 100%;
  height: 220px;
  display: block;
}

body {
  margin: 0;
  padding: 0;
}

#search-form {
  background: #00f;
  justify-content: center;
  padding: 15px 10px;
  display: flex;
}

.gallery {
  padding: var(--flex-items-gap);
}

.gallery__link {
  color: currentColor;
  text-decoration: none;
}

.gallery__link img {
  margin: 0 auto;
}

.info {
  justify-content: space-between;
  display: flex;
  box-shadow: 0 0 3px rgba(100, 100, 100, .5);
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  padding: 5px;
  display: flex;
}

